.book {
    width: 200px;
    margin: 15px;
    height: 375px;
    display: block;
    float: left;
}

.book img {
    width: 200px;
    height: 300px;
    border-radius: 0px;
    transition: 0.5s;
    -webkit-box-shadow: -15px 17px 10px -7px rgba(0,0,0,0.54);
    -moz-box-shadow: -15px 17px 10px -7px rgba(0,0,0,0.54);
    box-shadow: -15px 17px 10px -7px rgba(0,0,0,0.54);
}

.book img:hover {
    transform: scale(0.95);
} 

.book p {
    width: 90%;
    overflow: hidden;
    word-spacing: normal;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; 
            line-clamp: 2;
    -webkit-box-orient: vertical;
}

.img-skeleton {
    width: 200px;
    height: 300px;
    border-radius: 0px;
    transition: 0.5s;
    -webkit-box-shadow: -15px 17px 10px -7px rgba(0,0,0,0.54);
    -moz-box-shadow: -15px 17px 10px -7px rgba(0,0,0,0.54);
    box-shadow: -15px 17px 10px -7px rgba(0,0,0,0.54);
}

.skeleton {
    animation: skeleton-loading 1s linear infinite alternate;
}

.text-skeleton {
    width: 90%;
    height: 25px;
    border-radius: 5px;
}

@keyframes skeleton-loading {
    0% {
        background-color: hsl(200, 20%, 85%);
    }

    100% {
        background-color: hsl(200, 20%, 95%);
    }
}

@media (max-width: 700px) {
    .book {
        margin: 15px;
    }
}